<template>
  <div id="mobile-menu">
    <button @click="opened = !opened" class="hamburger">
      <label :class=" { hamnimation: opened } ">
        <b>
          <i></i>
          <i></i>
          <i></i>
        </b>
      </label>
    </button>
      <ul v-click-outside="hide" v-show="opened" class="mobile-menu">
        <router-link to="/" tag="li">
          <a id="nav-link">
            <span @click="opened = !opened">Home</span>
          </a>
        </router-link>
        <router-link to="/projectpage" tag="li" active-class="active">
          <a id="nav-link">
            <span @click="opened = !opened">Properties</span>
          </a>
        </router-link>
        <router-link to="/mycv" tag="li" active-class="active">
          <a id="nav-link">
            <span @click="opened = !opened">Area Guide</span>
          </a>
        </router-link>
        <router-link to="/contactme" tag="li" active-class="active">
          <a id="nav-link">
            <span @click="opened = !opened">The Brand</span>
          </a>
        </router-link>
        <router-link to="/contactme" tag="li" active-class="active">
          <a id="nav-link">
            <span @click="opened = !opened">Contact</span>
          </a>
        </router-link>
      </ul>
  </div>
</template>

<script>
import ClickOutside from "vue-click-outside";

export default {
  data() {
    return {
      opened: false,
    };
  },
  methods: { hide() {
      this.opened = false;
    },},
  events: {
    closeEvent: function () {
      this.hide();
    },
  },
  mounted() {
    // prevent click outside event with popupItem.
    this.popupItem = this.$el;
  },
  directives: {
    ClickOutside,
  },
};
</script>

<style lang="scss">
@import "../assets/styles/_variables.scss";

#mobile-menu {
  display: none;
  @media #{$large_mobile, $sm_mobile} {
    display: inline-block;
  }
  z-index: 9990 !important;
}
.hamburger {
  display: block;
  position: absolute;
  right: 10px;
  top: 30px;
  z-index: 10;
  border: 1px solid $p_color;
  background: rgb(0, 0, 0);
  border-radius: 5px;
  outline: none;

  //Ham icon
  label {
    margin: auto 0;
    position: relative;
    z-index: 900;
    cursor: pointer;
    border-right: none;

    b {
      width: 40px;
      height: 40px;
      display: block;
      position: relative;
      cursor: pointer;
      transition: background 200ms ease;

      i {
        display: block;
        background: $p_color;
        width: 30px;
        height: 4px;
        position: absolute;
        left: 5px;
        top: 11px;
        border-radius: 2px;
        transform-origin: 20px 4px;
        transition: transform 200ms cubic-bezier(0.09, 0.92, 0, 1.435),
          opacity 500ms ease;

        &:nth-child(2) {
          top: 18px;
        }

        &:nth-child(3) {
          transform-origin: 26px 4px;
          top: 25px;
        }
      }
    }
  }
}

.hamnimation {
  b {
    transition-delay: 100ms;

    i {
      background-color: #0acdff;

      &:nth-child(1) {
        transform: rotate(45deg) translate(5px, 9px);
      }

      &:nth-child(2) {
        opacity: 0;
      }

      &:nth-child(3) {
        transform: rotate(-45deg) translate(9px, -13px);
      }
    }
  }
}

.mobile-menu {
  position: absolute;
  right: 10px;
  top: 90px;
  width: 120px;
  height: 300px;
  background-color: rgb(0, 0, 0);
  border: 1px solid;
  border-color: $p_color;
  border-radius: 10px;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  z-index: 9999;
  transition: all 200ms ease-in-out;
  padding: 0;
  router-link {
    border-bottom: 1px solid white;
    border-color: $p_color;
    width: 100%;
    height: 4rem;
    padding: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    &:last-child {
      border: none;
    }
  }
  a {
    text-decoration: none;
    font-weight: 600;
    color: $p_color;
    font-size: 14px;
  }
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
</style>