    <div class="b-image"></div>
    <div class="brand">
      <h2 class="glitch" data-text="
Caspineon">Caspineon</h2>
      <p>Living Spaces Provider</p>
    </div>
    <div class="hero-title">
      <h3>Places to inhabit for everyone</h3>
      <p>Humans, Neopunks, Cybermen, Robots, Halfmen and more...</p>
      <a href="#house">
        <button class="button">
          <span class="button__text">Find yourself a place now!</span>
        </button>
      </a>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },

  computed: {},

  filters: {},
};
