<style lang="scss" scoped>
@import "../assets/styles/_variables.scss";
@mixin glitchCopy {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.glitch {
  position: relative;
  color: white;
  letter-spacing: 0.2em;
  animation: glitch-skew 1s infinite linear alternate-reverse;

  &::before {
    @include glitchCopy;
    left: 2px;
    text-shadow: -2px 0 #ff00c1;
    clip: rect(44px, 450px, 56px, 0);

    animation: glitch-anim 5s infinite linear alternate-reverse;
  }
  &::after {
    @include glitchCopy;
    left: -2px;
    text-shadow: -2px 0 #00fff9, 2px 2px #ff00c1;
    animation: glitch-anim2 1s infinite linear alternate-reverse;
  }
}

@keyframes glitch-anim {
  $steps: 20;
  @for $i from 0 through $steps {
    #{percentage($i*(1/$steps))} {
      clip: rect(random(100) + px, 9999px, random(100) + px, 0);
      transform: skew((random(100) / 100) + deg);
    }
  }
}

@keyframes glitch-anim2 {
  $steps: 20;
  @for $i from 0 through $steps {
    #{percentage($i*(1/$steps))} {
      clip: rect(random(100) + px, 9999px, random(100) + px, 0);
      transform: skew((random(100) / 100) + deg);
    }
  }
}

@keyframes glitch-skew {
  $steps: 10;
  @for $i from 0 through $steps {
    #{percentage($i*(1/$steps))} {
      transform: skew((random(10) - 5) + deg);
    }
  }
}

#hero {
  width: auto;
  height: 100vh;
  margin-top: -30vh !important;
  margin: 0 auto;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 24vh;

  .brand {
    h2 {
      font-size: 8vh;
      color: $c3;
      position: relative;
    }

    p {
      font-size: 4vh;
      color: rgb(37, 255, 255);
      letter-spacing: 0.1em;
    }
  }
  .hero-title {
    margin-top: 20vh;
    img {
      width: 50vh;
      height: auto;
      margin: 0 auto;
    }
    h3 {
      font-size: 8vh;
      color: white;
    }
    p {
      font-size: 3vh;
      color: white;
    }

    /* Button styles */

    .button {
      margin-top: 10vh;
      background: none;
      height: 8vh;
      padding: 0 26px;
      border: none;
      border-left: 5px solid $c2;
      cursor: pointer;
      transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
      display: flex;
      display: inline-flex;
      align-items: center;
      color: $c4;
      font-family: $s_font;
      position: relative;
      z-index: 1;
    }

    .button:before {
      content: "";
      transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
      background: $p_color;
      clip-path: polygon(
        0 0,
        100% 0,
        100% calc(100% - 10px),
        calc(100% - 10px) 100%,
        0 100%
      );
      position: absolute;
      top: -5px;
      right: -5px;
      bottom: -5px;
      left: -5px;
      z-index: -1;
      opacity: 0;
    }

    .button:after {
      content: "";
      transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
      background: $p_color;
      clip-path: polygon(
        0 0,
        100% 0,
        100% calc(100% - 15px),
        calc(100% - 15px) 100%,
        0 100%
      );
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: -1;
    }

    .button:hover,
    .button:focus {
      outline: none;
      border-left: 5px solid $p_color;
    }

    .button:hover:after,
    .button:focus:after {
      background: $c2;
    }

    .button:hover .button__icon {
      transform: translate3d(3px, 0, 0);
    }

    .button:active {
      transform: scale(1);
      transition-duration: 0.1s;
    }

    .button__text {
      font-size: 3vh;
      font-weight: 600;
    }
  }

  @media #{$large_mobile, $sm_mobile} {
    .brand {
      h2 {
        margin-top: 3vh;
        font-size: 6vh;
      }
      p {
        font-size: 3.5vh;
      }
    }
    .hero-title {
      width: 95%;
      margin-top: 10vh;
      h3 {
        font-size: 5vh;
      }
      p {
        font-size: 3vh;
      }
      a {
        margin-top: 5vh;
        button {
          width: 30vh;
          .button__text {
            font-size: 2.5vh;
          }
        }
      }
    }
  }
}
</style>